<i18n>
{
  "en": {
    "MyOrder": "Order",
    "All": "All",
    "Charger": "Charger",
    "Battery": "Battery",
    "ChargeTime": "Charge time",
    "TheLeaseTime": "The lease time",
    "address": "Zhongshan Avenue",
    "Unit": "￥",
    "AlreadyPaid": "Already paid",
    "Pay": "Pay",
    "NoConsumptionRecord": "No consumption record."
  },
  "zh": {
    "MyOrder": "我的订单",
    "All": "全部",
    "Charger": "座充",
    "Battery": "充电宝",
    "ChargeTime": "充电时长",
    "TheLeaseTime": "租借时长",
    "address": "中山大道",
    "Unit": "元",
    "AlreadyPaid": "已支付",
    "Pay": "去支付",
    "NoConsumptionRecord": "暂无任何消费记录"
  }
}
</i18n>

<template>
  <div class="order">
    <head-top :title='$t("MyOrder")' :go-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <div class="tabs">
        <div :class="['tab',{'active': currentTab === tab.type}]" v-for="tab in tabs" :key="tab.type" @click="toggleTab(tab.type)">
          <span>{{tab.title}}</span>
        </div>
      </div>
     <div class="content">
        <div class="list" v-if="list[currentTab].data&&list[currentTab].data.length">
          <scroll ref="scroll"
                  :data="list[currentTab].data"
                  :pullDownRefresh="true"
                  :pullUpLoad="true"
                  @pullingDown="onPullingDown"
                  @pullingUp="onPullingUp">
            <div class="warp">
              <div class="item" v-for="(item, index) in list[currentTab].data" :key="index">
                <div class="left">
                  <p>
                    <span class="text">{{item.lent_time}}</span>
                    <img v-show="item.charger_type === 1" src="../../assets/wallet_images/charger.png" alt="">
                    <img v-show="item.charger_type === 2" src="../../assets/wallet_images/baby.png" alt="">
                  </p>
                  <p>
                    <span class="duration">
                      <strong v-show="item.charger_type === 1 || true">{{ $t("ChargeTime") }}</strong>
                      <!-- <strong v-show="item.charger_type === 2">{{ $t("TheLeaseTime") }}</strong> -->
                      : {{item.duration}}
                    </span>
                  </p>
                  <p class="address">
                    <img src="../../assets/wallet_images/add.png" alt="">
                    <span>{{item.lent_adress}}</span>
                  </p>
                </div>
                <div class="right">
                  <span class="icon">
                    <img v-show="item.pay_state===2&&$i18n.locale==='zh'" src="../../assets/wallet_images/nopay.jpg" alt="">
                    <img v-show="item.pay_state===2&&$i18n.locale==='en'" src="../../assets/wallet_images/obligation.png" alt="">
                  </span>
                  <span :class="['count', item.pay_state===1? 'green' : 'red']">
                    <strong>{{item.fee / 100}}</strong>{{ $t("Unit") }}
                  </span>
                  <span class="result">
                    <button @click="payHandle(item)" v-show="item.pay_state===2">
                      {{ $t("Pay") }}
                    </button>
                    <strong v-show="item.pay_state===1">{{ $t("AlreadyPaid") }}</strong>
                  </span>
                </div>
              </div>
            </div>
          </scroll>
        </div>
        <div class="empty" v-else>
          <img src="../../assets/synerMax-skin/nothing@3x.png" alt="">
          <span>{{ $t("NoConsumptionRecord") }}</span>
        </div>
     </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
import {setStorage} from '../../utils/local'
export default {
  name: 'order',
  data () {
    return {
      tabs: [
        {
          title: this.$t('All'),
          type: 'all'
        },
        {
          title: this.$t('Charger'),
          type: 'charger'
        },
        {
          title: this.$t('Battery'),
          type: 'powerbank'
        }
      ],
      list: {
        all: {
          // 'type': 1,
          // 'page': 1,
          // 'count': 1,
          // 'total_page': 1,
          // 'data': [
          //   {
          //     'charger_type': 1,
          //     'duration': '2小时22分钟',
          //     'no': 'LENT2018120416580222590633934P',
          //     'pay_state': 1,
          //     'fee': 10,
          //     'lent_adress': '中山大道',
          //     'lent_time': '2018-12-04 16:58:03'
          //   }
          // ]
        },
        charger: {
          // 'type': 1,
          // 'page': 1,
          // 'count': 1,
          // 'total_page': 1,
          // 'data': [
          //   {
          //     'charger_type': 1,
          //     'duration': '2小时22分钟',
          //     'no': 'LENT2018120416580222590633934P',
          //     'pay_state': 1,
          //     'fee': 10,
          //     'lent_adress': '中山大道',
          //     'lent_time': '2018-12-04 16:58:03'
          //   }
          // ]
        },
        powerbank: {
          // 'type': 1,
          // 'page': 1,
          // 'count': 1,
          // 'total_page': 1,
          // 'data': [
          //   {
          //     'charger_type': 1,
          //     'duration': '2小时22分钟',
          //     'no': 'LENT2018120416580222590633934P',
          //     'pay_state': 1,
          //     'fee': 10,
          //     'lent_adress': '中山大道',
          //     'lent_time': '2018-12-04 16:58:03'
          //   }
          // ]
        }
      },
      currentTab: 'all',
      page: 1 // 页码
    }
  },

  components: {
    headTop
  },

  mounted () {
    console.log('order1')
    this.dataInteractionResponse(res => {
      // console.log(res)
      switch (res.msgType) {
        // 查询订单数据返回
        case '0x5222':
          console.log('order2')
          if (res.result && res.data) {
            if (this.page === 1) {
              this.list[this.currentTab] = res.data
            } else {
              res.data.data.map(i => {
                this.list[this.currentTab].data.push(i)
              })
            }
          }
          let billInfo = res.data
          console.log(billInfo.data)
          let unpayBill = [] // 未支付订单
          if (billInfo.data) {
            billInfo.data.forEach(element => {
              if (element.pay_state === 2) {
                unpayBill.push(element)
              }
            })
          }
          if (unpayBill.length > 0) {
            if (this.Version === 2) {
              this.$store.state.user.unpayed = true
            }
          } else {
            this.$store.state.user.unpayed = false
          }
          setStorage('unPaybill', unpayBill)
          break
      }
    })
    // 查询订单数据请求
    this.dataInteractionRequest({
      msgType: '0x5221',
      type: 3,
      page: this.page,
      count: 10
    })
    if (this.ISDEBUG) {
      let str = '{"data":{"data":[{"fee":1,"duration":"少于1分钟","lent_time":"2018-12-26 12:01:20","lent_adress":"广东省广州市天河区建中路3号","pay_state":2,"no":"LD20181226120112"}],"total_page":1,"count":10,"type":3,"page":1},"result":true,"msgType":"0x5222"}'
      window.dataInteractionResponse(str)
    }
  },

  methods: {
    onPullingDown () {
      console.log(111)
      // setTimeout(() => {
      //   this.$refs.scroll.forceUpdate()
      // }, 2000)
      this.page = 1
      this.updatePage(this.currentTab, this.page)
      if (this.ISDEBUG) {
        let requestType
        switch (this.currentTab) {
          case 'all':
            requestType = 3
            break
          case 'charger':
            requestType = 1
            break
          case 'powerbank':
            requestType = 2
            break
        }
        let str = `{"data":{"type":${requestType},"page":1,"count":1,"total_page":10,"data":[{"charger_type":1,"duration":"2小时22分钟","no":"LENT2018120416580222590633934P","pay_state":1,"fee":10,"lent_adress":"中山大道","lent_time":"2018-12-04 16:58:03"}]},"result":true,"msgType":"0x5222"}`
        window.dataInteractionResponse(str)
      }
    },
    onPullingUp () {
      if (this.list[this.currentTab].total_page > this.page) {
        this.updatePage(this.currentTab, ++this.page)
        if (this.ISDEBUG) {
          let requestType
          switch (this.currentTab) {
            case 'all':
              requestType = 3
              break
            case 'charger':
              requestType = 1
              break
            case 'powerbank':
              requestType = 2
              break
          }
          let str = `{"data":{"type":${requestType},"page":${this.page},"count":1,"total_page":10,"data":[{"charger_type":2,"duration":"2小时22分钟","no":"LENT2018120416580222590633934P","pay_state":1,"fee":10,"lent_adress":"中山大道","lent_time":"2018-12-04 16:58:03"}]},"result":true,"msgType":"0x5222"}`
          window.dataInteractionResponse(str)
        }
      } else {
        this.$refs.scroll.forceUpdate()
      }
    },
    goBackHander () {
      // console.log(123)
    },
    toggleTab (type) {
      // 回到顶部
      this.$refs.scroll && this.$refs.scroll.scrollTo(0, 0)
      this.currentTab = type
      // console.log(this.currentTab)
      this.page = 1
      this.updatePage(this.currentTab, this.page)
      if (this.ISDEBUG) {
        let requestType
        switch (this.currentTab) {
          case 'all':
            requestType = 3
            break
          case 'charger':
            requestType = 1
            break
          case 'powerbank':
            requestType = 2
            break
        }
        let str = `{"data":{"type":${requestType},"page":1,"count":1,"total_page":1,"data":[{"charger_type":2,"duration":"2小时22分钟","no":"LENT2018120416580222590633934P","pay_state":2,"fee":10,"lent_adress":"中山大道","lent_time":"2018-12-04 16:58:03"}]},"result":true,"msgType":"0x5222"}`
        window.dataInteractionResponse(str)
      }
    },
    // 更新第一页数据
    updatePage (type, page) {
      let requestType
      switch (type) {
        case 'all':
          requestType = 3
          break
        case 'charger':
          requestType = 1
          break
        case 'powerbank':
          requestType = 2
          break
      }
      // 查询订单数据请求
      this.dataInteractionRequest({
        msgType: '0x5221',
        type: requestType,
        page: page,
        count: 10
      })
    },
    payHandle (item) {
      // console.log(item)
      this.$router.push({
        name: 'orderBill',
        params: {
          item: item
        }
      })
    }
  },
  watch: {
    '$i18n.locale' (val, oldVal) {
      this.tabs[0].title = this.$t('All')
      this.tabs[1].title = this.$t('Charger')
      this.tabs[2].title = this.$t('Battery')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.order{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .tabs{
      display: flex;
      .tab{
        height: 80px;
        flex: 1;
        font-size: 30px;
        &.active{
          color: @themeColor;
          span::after{
            content: '';
            display: block;
            .wh(60px,6px);
            background-color: @themeColor;
            margin: -14px auto 0 auto;
          }
        }
        span{
          display: bloink;
          line-height: 80px;
        }
      }
    }
    .content{
      .wh(100%, calc(~"100% - 80px"));
      background: #eeeeee;
      overflow-y: scroll;
      .empty{
        img{
          .wh(146px, 187px);
          margin: 270px auto 0 auto;
        }
        span{
          display: block;
          font-size: 30px;
          margin-top: 10px;
        }
      }
      .list{
        .wh(100%, 100%);
        .warp{
          .wh(100%, 100%);
          padding-top: 30px;
          .item{
            &:nth-child(1){
              margin: 0 auto;
            }
            &:last-child{
              margin-bottom: 20px;
            }
            margin: 44px auto 0 auto;
            .wh(690px, 210px);
            background: #ffffff;
            border-radius: 20px;
            box-shadow: 0px 4px 25px rgba(130,130,130,0.2); // 左上下右
            display: flex;
            .left{
              .wh(520px, 100%);
              p{
                font-size: 30px;
                height: 74px;
                color: #595959;
                line-height: 74px;
                text-align: left;
                padding-left: 30px;
                img{
                  height: 36px;
                  margin-left: 20px;
                  vertical-align: text-bottom;
                }
                .duration{
                  font-size: 32px;
                  color: #585858;
                }
              }
              p:nth-child(1){
                line-height: 80px;
              }
              .address{
                font-size: 28px;
                line-height: 50px;
                img{
                  height: 30px;
                  margin: 0 20px 0 0;
                }
                span{
                  font-size: 26px;
                  color: #A9A9A9;
                }
              }
            }
            .right{
              width: 180px;
              padding: 0 30px 0 0 ;
              span{
                display: block;
                font-size: 20px;
                color: #999999;
                &.icon{
                  height: 54px;
                  img{
                    height: 54px;
                  }
                }
                &.count{
                  font-size: 30px;
                  .wh-l(100%, 80px);
                  strong{
                    font-size: 52px;
                    margin-right: 10px;
                  }
                }
                &.result{
                  .wh-l(100%, 88px);
                  button{
                    .wh-l(110px, 50px);
                    color: #ffffff;
                    background:@themeColor;
                    border-radius: 8px;
                    font-size: 26px;
                    position: relative;
                    top: -20px;
                  }
                  strong{
                    .wh(100%, 100%);
                    display: block;
                    line-height: 30px;
                  }
                }
                &.red{
                  color: #f3223a;
                }
                &.green{
                  color: #42c574;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



